.about-me-wrap {
    width: 100%;
    overflow: overlay;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #1f242d;
    width: 100%;
    overflow: overlay;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.about-me {
    height: fit-content;
    width: 50%;
    padding: 20px;
    margin: 90px 0 20px 0;
    background-color: #2a303c;
    border-radius: 10px;
}
.about-me-header {
    margin: 10px 0 20px 5%;
    font-size: 2em;
    width: 100%;
}
.about-me-wrap p {
    line-height: 130%;
    font-size: 1.2em;
    text-indent: 50px;
    padding: 10px 20px 0 10px;
}
.self-portrait {
    width: 400px;
    height:400px;
    object-fit: cover;
    margin: 90px 20px 0 0;
    border-radius: 10px;
}