.header {
    height: 74px;
    width: 100%;
    background-color: rgba(91, 91, 91, 0.57);
    display: flex;
    justify-content: space-between;
    padding: 30px 10px 15px 0;
    position: fixed;
    z-index: 1;
    transition: 0.4s;
    border-bottom: 2px solid var(--drk-beige);
    padding-top: 15px;
}
#my-name-label-border {
    display: flex;
    align-items: center;
    width: 463px;
    height: 74px;
    background-color: rgb(49, 33, 0);
    border: 1px solid var(--drk-beige);
    border-radius: 0 200px 200px 0;
    transition: 0.4s;
    filter: drop-shadow(0 0 0.65rem rgb(133, 97, 11));
}
.my-name-label {
    width: 460px;
    height: 70px;
    background-color: var(--background-black);
    border-radius: 0 200px 200px 0;
    transition: 0.4s;
}
.my-name {
    color: var(--background-black);
    padding: 8px 20px 0px 80px;
    transition: 0.4s;
    font-size: 400%;
}
.name-tag {
    color: white;
}

.nav {
    width: 550px;
    margin-right: 5%;
}
.nav-tabs {
    font-size: 150%;
    display: flex;
    justify-content: space-between;
    align-content: center;
    margin-right: 10px;
    font-weight: bold;
    transition: 0.4s;
}
.nav-items {
    border-bottom: 3px solid transparent;
    text-align: center;
    height: 20px;
    padding: 30px 25px 15px 25px;
    color: #E1D5B8;
}
.nav-link {

}
#nav-ul-active:hover {
    border-bottom-color: #E1D5B8;
    border-style: ridge;
}
.nav-link-active {
    border-bottom-color: #E1D5B8;
    border-style: ridge; 
}